.page-hero-section {
  h1 {
    margin-bottom: 30px;
  }
  @media screen and (max-width: 1119px) {
    br {display: none;}
  }
}//.page-hero-section


.services-section {

  .services-row {
    @media screen and (min-width: 980px) and (max-width: 1100px) {
      flex-wrap: wrap;
      justify-content: space-between;
      &:after {content: none;}
    }

    .title-row {

    }

    .et_pb_column {
      @media screen and (min-width: 981px) {
        margin-right: 1.5%;
        width: 19.1%;
      }
      @media screen and (min-width: 980px) and (max-width: 1100px) {
        margin-bottom: 30px;
        width: 47.25%;
      }

      .service-item {
        min-height: 300px;
        position: relative;
        @media screen and (min-width: 1101px) and (max-width: 1300px) {
          font-size: 14px;
        }
        &:after {
          background-position: center;
          background-repeat: no-repeat;
          background-size: contain;
          bottom: 0;
          content: '';
          position: absolute;
          right: 0;
          z-index: 0;
        }
        &.research:after {
          background-image: url("data:img/png;base64,");
          height: 37.7777%;
          max-height: 102px;
          max-width: 102px;
          transform: translate(21%, 19%);
          width: 37.7777%;
        }//&.research:after
        &.customer-service:after {
          background-image: url("data:img/png;base64,");
          height: 33.7037%;
          max-height: 91px;
          max-width: 91px;
          transform: translate(4%, 8%);
          width: 33.7037%;
        }//&.customer-service:after
        &.marketing:after {
          background-image: url("data:img/png;base64,");
          height: 37.7777%;
          max-height: 101px;
          max-width: 102px;
          transform: translate(30%, 10%);
          width: 37.7777%;
        }//&.marketing:after
        &.government-relations:after {
          background-image: url("data:img/png;base64,");
          height: 36.2962%;
          max-height: 99px;
          max-width: 98px;
          transform: translate(13%, 5%);
          width: 36.2962%;
        }//&.government-relations:after
        &.hr:after {
          background-image: url("data:img/png;base64,");
          height: 38.1481%;
          max-height: 103px;
          max-width: 103px;
          transform: translate(13%, 9%);
          width: 38.1481%;
        }//&.hr:after

        .et_pb_text_inner {z-index: 1;}

      }//.service-item

      h3 {
        margin-bottom: -4px;
        min-height: 66px;
        @media screen and (min-width: 1101px) and (max-width: 1300px) {
          font-size: 18px;
          line-height: 21px;
        }
      }//h3

    }//.et_pb_column

  }//.services-row


}//.services-section




.video-gallery-section {

  h3 {
    margin-bottom: 12px;
    span {white-space: nowrap;}
  }

  .video-gallery-row {
    @media screen and (min-width: 980px) and (max-width: 1024px) {
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;
      &:after {content: none;}
    }
    @media screen and (min-width: 480px) and (max-width: 980px) {
      overflow: visible;
      padding: 0 50px 30px !important;
      width: 100%;
    }


    .title-row {
      @media screen and (max-width: 1235px) {

      }
    }//.title-row

    .et_pb_column {
      position: relative;
      &:hover .et_pb_image {

        @media screen and (min-width: 1024px) {transform: scale(1.09);}
        @media screen and (min-width: 980px) and (max-width: 1024px) {transform: scale(1.03);}
        @media screen and (min-width: 480px) {box-shadow: 0 0 50px 0 rgba(97, 106, 223, 43);}

        &:before {opacity: 1;}
      }
      @media screen and (min-width: 1024px) {
        margin-right: 2.4%;
        width: 23.20%;
      }
      @media screen and (min-width: 980px) and (max-width: 1024px) {
        margin-right: 0;
        width: 47.25%;
      }

      .et_pb_image {
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        transition: all .3s ease;
        &:before {
          background-color: rgba(97,106,223,.85);
          background-image: url("data:img/png;base64,");
          background-position: center 40%;
          background-repeat: no-repeat;
          background-size: 18.6486% 31.9819%;
          bottom: 0;
          content: '';
          left: 0;
          opacity: 0;
          position: absolute;
          right: 0;
          top: 0;
          transition: all .3s ease;
          z-index: 1;
        }
        @media screen and (min-width: 980px) and (max-width: 1024px) {margin-bottom: 20px !important;}

        .et_pb_image_wrap {display: block;}

        img {width: 100%;}

      }//.et_pb_image

      .image-title {
        bottom: 12%;
        position: absolute;
        text-align: center;
        text-transform: uppercase;
        width: 100%;
        z-index: 1;
        h4 {
          line-height: 28px;
          padding: 0;
          @media screen and (max-width: 1750px) {font-size: 20px;}
          @media screen and (min-width: 1350px) and (max-width: 1500px) {font-size: 18px;}
          @media screen and (min-width: 1200px) and (max-width: 1350px) {font-size: 16px;}
          @media screen and (min-width: 1024px) and (max-width: 1200px) {font-size: 14px; line-height: 18px;}
          @media screen and (max-width: 700px) {font-size: 16px; line-height: 18px;}
          @media screen and (max-width: 600px) {font-size: 14px; line-height: 16px;}
          @media screen and (max-width: 480px) {font-size: 20px; line-height: 22px;}
        }
      }//.image-title
    }//.et_pb_column

  }//.video-gallery-row


}//.video-gallery-section