//HERO SECTION
/////////////////////////////////////////////////////////////////////////////

#main-content .hero-section {    
  @media screen and (min-width: 981px) {
    min-height: 664px;
    padding-top: 190px !important;
  }
  @media screen and (max-width: 980px) {padding-top: 80px;}
  .hero-row {
    @media screen and (max-width: 768px) {
      .et_pb_column {width: 66%;}
    }
  }
    
  
  .hero-title {
    border-top: 1px solid #adc3e1;
    margin-right: 80px;    
    @media screen and (max-width: 1820px) {margin-left: 0 !important;}
    @media screen and (max-width: 1615px) {margin-right: 60px;}
    @media screen and (max-width: 1450px) {margin-right: 20px;}
    @media screen and (max-width: 1200px) {margin-right: 0;}    
    @media screen and (max-width: 980px) {margin-left: 0 !important;}
    @media screen and (max-width: 768px) { }
  }
  h1 {
    overflow: hidden;  
    word-break: normal;
    word-wrap: normal;
    @media screen and (min-width: 768px) {white-space: nowrap;}
    @media screen and (max-width: 1799px) {font-size: 65px !important;}
    @media screen and (max-width: 1490px) {font-size: 60px !important;}
    @media screen and (max-width: 1300px) {font-size: 50px !important; line-height: 50px;}
    @media screen and (max-width: 998px) {
      line-height: 48px;
      overflow: visible; 
      text-align: left !important;
    }
    @media screen and (max-width: 375px) {text-align: center !important;}
    small {
      display: block;
      float: right;
      font-size: 44px;
      font-weight: 500;
      line-height: 48px;
      position: relative;
      text-align: right;  
      @media screen and (max-width: 1799px) {font-size: 34px;}
      @media screen and (max-width: 1490px) {font-size: 30px !important;}
      @media screen and (max-width: 1300px) {font-size: 26px !important;}
      @media screen and (max-width: 998px) {
        float: none;
        font-size: 30px;
        text-align: left;
      }
      @media screen and (max-width: 375px) {
        font-size: 26px;
        text-align: center;
      }     
      &:before {
        background: #adc3e1;
        bottom: 0;
        content: '';
        height: 1px;
        margin: auto 10px auto auto;;
        position: absolute;
        right: 100%;
        top: 7px;
        width: 200%;
        @media screen and (max-width: 998px) {content: none;}
      }
    }
  }//h1
  
  .cta-module {
    align-items: center;
    display: flex;
    justify-content: flex-end;
    margin-right: 80px;
    @media screen and (max-width: 1615px) {margin-right: 60px;}
    @media screen and (max-width: 1450px) {margin-right: 20px;}
    @media screen and (max-width: 1309px) {
      align-items: flex-end;
      flex-direction: column;
    }    
    @media screen and (max-width: 1200px) {margin-right: 0;}
    @media screen and (max-width: 998px) {
      align-items: flex-start;
      text-align: left;
    }
    @media screen and (max-width: 375px) {
      align-items: center;
      transform: translateY(10px);
      text-shadow: 0 0 35px #fff;
    }
    
    .et_pb_promo_description {
      padding-bottom: 0;      
      max-width: 255px;
      @media screen and (max-width: 1309px) {
        margin-bottom: 10px;
        max-width: 100%;
      }
      h2 {
        max-width: 300px;
        padding: 0;
        @media screen and (max-width: 1470px) {font-size: 22px;}        
        @media screen and (min-width: 710px) and (max-width: 980px) {max-width: 60%;}        
        @media screen and (max-width: 375px) {text-align: center;}        
      }
    }//.et_pb_promo_description 
    
    .et_pb_promo_button {
      align-items: center;
      background-image: linear-gradient(to right, #6f76e5, #4653cf);
      display: flex;
      height: 94px;
      line-height: 30px !important;
      margin-left: 25px;
      padding-right: 10px !important;
      text-shadow: none;
      white-space: nowrap;
      @media screen and (max-width: 998px) {margin-left: 0;}
      @media screen and (max-width: 768px) {
        font-size: 20px;
        height: 60px;
        font-size: 16px !important;
      }
      @media screen and (max-width: 425px) {font-size: 14px !important;}
      &:after {
        background: url('data:image/svg+xml;utf8,<svg viewBox="40 44 74 78" xmlns="http://www.w3.org/2000/svg"><title>arrow</title><path d="M63.837,54.969 C58.462,51.877 54.094,54.405 54.094,60.610 L54.094,106.385 C54.094,112.592 58.462,115.119 63.837,112.034 L103.847,89.086 C109.225,86.002 109.225,80.994 103.847,77.909 L63.837,54.969 Z" fill="#fff"/></svg>') no-repeat center;
        content: '';
        height: 78px;
        margin-left: 0 !important;
        position: static;
        transform: none;
        width: 74px;
        @media screen and (max-width: 768px) {
          height: 50px;
          width: 50px;
        }
        @media screen and (max-width: 425px) {
          height: 40px;
          width: 40px;
        }
      }
      &:hover {
        background-image: linear-gradient(to right, darken(#6f76e5, 5%), darken(#4653cf, 5%));
        border-color: darken(#6871e1, 5%) !important;
      }
    }
  }//.cta-module 
}//.hero-section

//HERO SECTION END
/////////////////////////////////////////////////////////////////////////////



//OPINION SECTION
/////////////////////////////////////////////////////////////////////////////

.opinion-section {
  
  h3 {
    margin-bottom: 5px;
    @media screen and (max-width: 1232px) {      
      max-width: 700px;
      margin: auto;
    }
    @media screen and (min-width: 980px) {line-height: 50px !important;}
    @media screen and (max-width: 980px) {
      max-width: 500px;
    }
  }//h3
  
  .opinion-item {
    align-items: center;
    display: flex;
    height: 261px;
    @media screen and (max-width: 479px) {width: 50% !important;}
    @media screen and (max-width: 480px) {height: 200px !important;}
    .item-title {      
      padding-bottom: 0;      
      @media screen and (min-width: 561px) {
        border-left: 1px solid #fff;
        padding-left: 20px;
      }      
      @media screen and (min-width: 1401px) and (max-width: 1700px) {font-size: 50px;}
      @media screen and (min-width: 1171px) and (max-width: 1400px) {font-size: 40px;}      
      @media screen and (min-width: 981px) and (max-width: 1170px) {font-size: 30px;}      
      @media screen and (min-width: 561px) and (max-width: 980px) {font-size: 40px;}         
      @media screen and (min-width: 480px) and (max-width: 560px) {font-size: 35px;}              
      @media screen and (min-width: 319px) and (max-width: 480px) {font-size: 25px;}  
      //@media screen and (max-width: 374px) {font-size: 50px;}      
      span {
        display: block;
        font-size: 28px;
        font-weight: normal;
        line-height: 22px;
        margin-top: 19px;
        @media screen and (max-width: 1400px) {font-size: 22px;}             
        @media screen and (max-width: 480px) {font-size: 14px; margin-top: 0;} 
      }
    }//.item-title
  }//.opinion-item 
}//.opinion-section


//OPINION SECTION END
/////////////////////////////////////////////////////////////////////////////



