//TESTIMONIAL SECTION
/////////////////////////////////////////////////////////////////////////////

.testimonial-section {
  @media screen and (min-width: 981px) {
    padding-top: 140px !important;
  }
  .testimonial-row {
    
    overflow: visible;
    @media screen and (max-width: 768px) {
      
    }
    &:before {
      background: url('data:img/png;base64,') no-repeat center;
      content: '';
      height: 59px;
      margin: auto;
      left: 0;
      position: absolute;
      right: 0;
      top: -27px;
      width: 75px;
    }
  }
  h6 {padding: 0;}
}//.testimonial-section 

//TESTIMONIAL SECTION END
/////////////////////////////////////////////////////////////////////////////







//CTA SECTION
/////////////////////////////////////////////////////////////////////////////

.cta-section {
  background-color: transparent;
  min-height: 205px;
  @media screen and (max-width: 980px) {
    background-size: cover !important;    
  }
  &:before {
    background-image: linear-gradient(to right, #353e49 40%, #313a45 60%);
    content: '';
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
  }
  .cta-row {
    align-items: center;
    display: flex;
    justify-content: space-between;
    height: 100%;
    min-height: 205px;
    &:after {content: none;}
    > div {
      width: auto;
      @media screen and (max-width: 1735px) {max-width: 850px;}
    }
    
    @media screen and (max-width: 980px) {
      flex-direction: column;
      .et_pb_text_inner {
        text-align: center;
        @media screen and (max-width: 550px) {
          font-size: 40px;
          p {line-height: 50px;}
        }
        @media screen and (max-width: 375px) {
          font-size: 30px;
          p {line-height: 40px;}
        }
      }
    }
    
  }//.cta-row
}//.cta-section


//CTA SECTION END
/////////////////////////////////////////////////////////////////////////////





//VIDEO SECTION
/////////////////////////////////////////////////////////////////////////////

.video-section {
  
  > div {
    @media screen and (max-width: 1100px) {width: 90%;}
    @media screen and (max-width: 475px) {
      width: 100%;
      .et_pb_text_inner {
        padding: 0 20px;
        text-align: center;
      }
    }
  }// > div
  
  .video-module,
  .fluid-width-video-wrapper {
    @media screen and (min-width: 1200px) {
      min-height: 435px;
    }    
  }
  .fluid-width-video-wrapper:before,
  .et_pb_video_overlay_hover:before {
    background: url(assets/img/logo2.png) no-repeat center;
    background-size: contain;
    bottom: 45px;
    content: '';
    height: 20.6896%;
    position: absolute;
    right: 35px;
    width: 23.0543%;
    z-index: 10;
  }
  
