    height: 34.4827%;
    margin: 0;
    transform: translate(-50%, -50%);
    opacity: .7;
    width: 22.0264%;
    &:hover {opacity: 1;}
    &:before {content: none;}
  }
  
}//.video-section


//VIDEO SECTION END
/////////////////////////////////////////////////////////////////////////////






.cta-footer {
  @media screen and (max-width: 1110px) {
